.home-8
	@apply relative
	@screen xl
		&::before
			content: ""
			@apply absolute left-2/4 -translate-x-2/4 h-full rem:w-[1px]  bg-Utility-200 top-0
	@screen -md
		@apply py-10
	.wrapper
		.col-left,.col-right
			.img
				@apply h-full
				a
					@apply h-full
			.content
				.wrap-bottom
					.wrap-social
						.item
							a
								+hover-underline(95%, #000)
								@apply body-2 font-bold font-secondary