.btn
	@apply cursor-pointer select-none transition-all duration-300
	&.loading
		@apply bg-transparent #{!important}
		span,.icon
			@apply opacity-0
		&::before
			content: ''
			animation-duration: 0.5s
			@apply w-5 h-5 border-3 border-white/40 border-t-white rounded-full inline-block
			@apply absolute animate-spin
	&.btn-sw-1
		@apply sq-10 py-2 px-5 rounded-full  flex-center bg-transparent
		@apply border border-Utility-Black
		&::before
			@apply font-awesome text-Utility-Black font-light
		&.btn-prev
			&::before
				content: '\f053'
		&.btn-next
			&::before
				content: '\f054'
				@apply font-awesome
		&.secondary
			@apply bg-black/20 border-transparent
			&::before
				@apply text-Utility-white
		&:hover
			@apply bg-Utility-Black border-transparent
			&::before
				@apply text-Utility-white
	&.btn-add-cart
		@apply inline-flex items-center gap-3 text-Utility-500
		span
			@apply font-secondary body-1
		.icon
			i
				@apply text-xl font-light
		&:hover
			@apply text-Primary-1
			.icon
				i
					@apply text-Primary-1
	&.btn-primary
		@apply inline-flex items-center justify-center
		@apply border border-black rounded-full
		@apply h-12  px-9 bg-Utility-white  
		&:hover
			@apply text-white border-transparent bg-Primary-1
			span
				@apply text-Utility-white
		span
			@apply rem:text-[18px] font-bold text-black font-secondary uppercase
	&.btn-secondary
		backdrop-filter: blur(10px)
		background: rgba(0, 0, 0, 0.16)
		@apply inline-flex items-center justify-center
		@apply border rounded-full
		@apply border-white/25
		@apply h-12  px-9 bg-transparent text-white font-secondary uppercase
		&:hover
			@apply bg-white/80 border-white/25 text-Primary-1
			span
				@apply text-Primary-1
		span
			@apply font-secondary font-bold