.home-3
	.wrapper-main
		.wrapper-inner
			.col-left
				.wrap-button-slide
					.btn-prev
						@apply mr-[-4rem] #{!important}
					.btn-next
						@apply ml-[-4rem] #{!important}
				.swiper
					.swiper-slide
						.video-item, .img
							@apply h-full relative
							a
								@apply h-full
							.play-icon
								backdrop-filter: blur(10px)
								@apply absolute top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2 z-20
								@apply text-white bg-black/20 inline-flex items-center justify-center
								@apply rem:w-[68px] rem:h-[68px]  rounded-full heading-1
								i
									@apply font-light
			.col-right
				@apply xl:py-12 xl:rem:pl-[120px] p-5
				.wrap-content-top
					.title
						@apply heading-1
					.desc
						@apply font-secondary
				.wrap-content-bottom
					.product
						.img
							a
								@screen -xl
									@apply pt-[80%]
								img
									@apply object-contain
			&:nth-child(even)
				.col-right
					@apply xl:rem:pr-[120px] xl:pl-0 border-b border-b-black/20
				