.header-nav-top 
	@apply rem:h-[40px] bg-Primary-1 text-center text-white body-1 rem:gap-[10px]
	@apply inline-flex items-center justify-center w-full font-normal
	@screen -xl
		@apply text-[14px]
	p
		@apply font-normal font-secondary
	.icon
		@apply flex-center
.header
	transition: background-color 0.3s ease-in-out, color 0.3s ease-in-out
	@apply bg-transparent fixed left-0 right-0 z-100 top-0 text-white
	.header-wrapper
		@apply flex items-center justify-between relative
		@apply xl:rem:h-[110px] h-25 xl:py-0 md:py-3
		.wrapper-mobile
			@apply xl:hidden flex items-center gap-5
			.header-button-website
				@screen -md
					@apply hidden
		.header-logo
			@apply md:rem:w-[258px] w-[8.5rem] rem:h-[78px]
			@apply absolute left-2/4 -translate-x-2/4 top-2/4 -translate-y-2/4
			a
				@apply w-full h-full block
				img
					@apply w-full h-full object-contain
					transition: opacity 0.3s ease-in-out
					&.logo-default
						@apply w-full h-full opacity-100 object-contain
					&.logo-active
						@apply opacity-0 hidden
		.header-left
			@apply h-full
		.header-right
			@apply flex items-center gap-base h-full
			.header-right-inner
				@apply flex items-center gap-4
				.header-button-website
					@apply hidden xl:flex
				.header-auth
					.header-user
						@apply md:flex hidden
					>div
						@apply relative flex items-center
						a
							@apply text-xl
		
						&:not(:last-child)
							&::after
								content: ''
								@apply xl:mx-5 mx-2 rem:w-[1px] h-5 bg-white/20
								transition: background-color 0.3s ease-in-out
								@screen -md
									@apply hidden
					@apply flex items-center gap-2
					.header-search
						@apply inline-flex items-center cursor-pointer
						@apply sq-5 rounded-5
						i
							@apply pointer-events-none text-xl
		.header-language
			@apply relative
			&:hover
				.header-language-active
					&::after
						@apply rotate-180
				.header-language-list
					// c ngn ng th m ra li dng ny
					@apply opacity-100 pointer-events-auto
					.wpml-ls
						@apply p-0
			.header-language-active
				@apply flex items-center
				@apply rem:text-[16px]
				&::after
					content: '\f078'
					@apply font-awesome font-light ml-2
					@apply transition-all duration-300 text-xl
				li:not(.wpml-ls-current-language)
					@apply hidden
				.wpml-ls 
					@apply p-0
		.header-language-list
			@apply absolute top-full left-0
			@apply  text-black bg-transparent 
			@apply p-1 py-1 rem:text-[14px]
			@apply opacity-0 transition-300  pointer-events-none 
			// @apply shadow-dropshadow-light
			li
				a
					+hover-underline(95%, #fff)
				&.wpml-ls-current-language
					@apply hidden
	.header-menu
		@apply h-full flex
		.header-button-website
			@apply xl:hidden flex
			
			@screen -xl
				.btn-website
					@apply bg-transparent border-Primary-1
		@screen xl
			ul.header-nav
				@apply flex justify-center items-center rem:gap-[70px]
				>li							
					@apply relative h-full  
					@apply pb-1 border-b border-b-transparent
					@apply inline-flex flex-col justify-center
					transition: border-color 0.3s ease-in-out
					>a						
						@apply inline-flex items-center text-white
						@apply rem:text-[18px] font-bold uppercase font-secondary
						transition: color 0.3s ease-in-out
					// &.dropdown-product 
					// 	&:hover
					// 		.dropdown-product-container
					// 			@apply opacity-100 pointer-events-auto translate-y-0
					.dropdown-product-container
						@apply absolute -left-full top-full rem:w-[864px]
						@apply opacity-0 -translate-x-full pointer-events-none transition-all duration-500
						@apply grid grid-cols-[40.3%_1fr]
						.close-megamenu
							@apply absolute top-0 right-0 cursor-pointer
							@apply sq-15 flex-center bg-Primary-1 text-white
							i
								@apply text-white text-xl
						&.active
							@apply opacity-100 pointer-events-auto translate-x-0
						.column-left
							@apply bg-Utility-950 pt-20 px-15
							.dropdown-product-title 
								@apply mb-6 text-white/50 body-4 font-secondary
						.nav-solution-product 
							>ul
								@apply flex flex-col gap-6
								>li
									.name-category
										@apply mb-1 body-1 text-white font-extrabold
									>a
										@apply body-1 text-white/50 
										@apply border-b-transparent border-b font-secondary
										@apply items-center justify-between font-normal inline-flex
										span
											@apply body-1
										&::after
											content: none !important
										.icon
											@apply sq-6 inline-flex items-center justify-center opacity-0 transition-opacity
											i
												@apply text-white
										&.active
											@apply text-white border-b-white
											.icon
												@apply opacity-100 text-white
									&:hover
										a
											@apply border-b-white
										.icon
											@apply opacity-100
							ul.submenu-group
								li
									a
										@apply rem:text-[18px] font-normal text-white 
						.column-right
							@apply bg-Primary-1 py-20 px-15
							.tabslet-content-menu
								display: none
								.tabslet-content-menu-lists
									@apply grid grid-cols-2 gap-4
									.product-item
										@apply rem:px-[8px]
										.img
											@apply rem:w-[160px] mx-auto
										.content
											@apply mt-1
											.title
												@apply text-white font-secondary font-light text-base text-center
					&:hover
						@apply border-b-black
						&>a
							@apply text-Primary-1 
						&>ul
							@apply opacity-100 pointer-events-auto
					&[class*='current']
						&>a
							@apply text-Primary-1 
					&.menu-item-has-children
						&>a
							&::after
								content: '\f107'
								@apply font-awesome font-normal rem:text-[14px] ml-1
					&>ul
						background: rgba(0, 0, 0, 0.30)
						backdrop-filter: blur(2px)
						@apply rem:min-w-[360px]
						@apply top-full left-0 absolute
						@apply opacity-0 transition-all pointer-events-none
						&>li
							@apply py-3 px-6
							a
								@apply rem:text-[18px] font-normal inline-block text-white
		@screen -xl
			height: calc(100dvh - var(--header-height))					
			@apply fixed top-[var(--header-height)] left-0 w-[85%]  z-[100]
			@apply flex flex-col gap-6 p-[15px] overflow-y-auto
			@apply opacity-0 -translate-x-full transition-all duration-700 bg-white shadow-shadow-1
			&>ul.header-nav
				@apply flex flex-col gap-3
				li
					@apply relative
					a
						@apply text-[18px] font-medium  block pb-0 text-black font-secondary
					&.active
						.icon-arrow
							&::before
								@apply rotate-180
					&.dropdown-product 
						.dropdown-product-container
							.close-megamenu
								@apply hidden
							.column-left
								.dropdown-product-title
									@apply hidden
								.nav-solution-product
									>ul
										@apply flex flex-col bg-Primary-1 mt-1
										>li
											.name-category
												@apply hidden
											>a
												@apply text-white px-3 py-1.5 text-lg
							.column-right
								@apply hidden
					.menu-item
						@apply flex items-center relative
					.icon-arrow
						@apply absolute top-0 right-0 h-full 
						@apply px-1 text-3xl font-normal
						@apply flex-center
						&::before
							content: '\f078'
							transform-origin: center center
							will-change: transform
							@apply font-awesome text-[20px] font-light ml-2
							@apply transition-300
					&.menu-item-has-children				
						&>a
							@apply relative 
							@apply flex items-center justify-between
							&.dropdown-active
								&::after
									@apply rotate-180
							&::after
								content: '\f0d7'
								@apply font-awesome font-black text-lg
						>ul
							background-color: rgb(212 192 161)
							@apply hidden
							>li
								>a
									@apply border-none p-[8px] 
								&[class*='current']
									&>a
										@apply text-black
	&:hover
		@extend .header-active
	&.active
		@extend .header-active
	.header-bar
		@apply sq-10 flex-shrink-0 
		@apply items-center justify-center xl:hidden flex
		@apply  bg-white text-2xl text-black
		&.active
			i
				&::before
					content: "\f00d"
.header-active
	@apply bg-white text-black shadow-shadow-1
	.header-wrapper
		.wrapper-mobile
			.header-bar
				@apply bg-Primary-1 text-white
	.header-menu
		ul.header-nav
			>li
				>a
					@apply text-black
	.header-right
		.header-right-inner
			.header-button-website
				button.btn-website
					@apply border border-Primary-1 text-Primary-1 bg-white
					&:hover
						@apply bg-Primary-1 text-white
			.header-auth
				@screen xl
					>div
						&:not(:last-child)
							&::after
								@apply bg-Utility-100
	.header-logo
		a
			img.logo-active
				@apply opacity-100 block
			img.logo-default
				@apply opacity-0 hidden
				filter: brightness(0) saturate(0%)
.header-overlay
	position: fixed
	opacity: 0
	transition: 0.3s all ease-in-out
	pointer-events: none
	top: 0
	left: 0
	width: 100%
	height: 100%
	background-color: rgba(0, 0, 0, 0.5)
	z-index: 50
body.isOpenMenu
	.header-menu
		@apply opacity-100 translate-x-0 #{!important}
	.header-overlay
		@apply opacity-100 pointer-events-auto
body.isOpenSearch
	.header-search-form
		@apply opacity-100 pointer-events-auto translate-y-0
body
	&:has(.page-banner-main), &:has(.global-breadcrumb)
		header
			@extend .header-active
			@apply sticky top-0
			.header-wrapper
				.header-logo
					a
						img.logo-active
							@apply opacity-100 block
						img.logo-default
							@apply opacity-0 hidden
							filter: brightness(0) saturate(0%)
