.product-detail-1
	.product-detail-main
		@apply xl:rem:pl-[116px] px-4
	.format-content
		p
			@apply mb-2 
	.swiper-slide
		.img
			a
				@apply  bg-neutral-100 h-full
	.thumb
		@apply xl:py-13 py-10 lg:py-11
		@screen -lg
			@apply py-[3rem]
		@screen -md
			@apply py-10
		.swiper
			@apply absolute inset-0
			.swiper-slide
				.img
					@apply h-full relative
					a				
						img
							@apply border border-transparent
					.play-icon
						backdrop-filter: blur(10px)
						@apply absolute top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2 z-20
						@apply bg-black/20 md:sq-17 sq-10 inline-flex items-center justify-center rounded-full heading-1 font-light text-white
			.swiper-slide-thumb-active
				.img
					a
						img
							@apply border-Utility-500
	.main
		@apply overflow-hidden relative
		.img
			a
				@apply ratio:pt-[727_918]
				@screen -md
					@apply pt-[104%]
		.arrow-button
			@apply flex items-center gap-6 absolute bottom-0 left-0 p-6 z-10
	.col-left
		@apply xl:sticky xl:top-25 h-fit
	.col-right
		@apply bg-Utility-100 flex flex-col justify-center
		@screen -md
			@apply p-5
		.wrapper-content
			.wrap-info-product-price
				button
					@apply h-12 inline-flex items-center justify-center px-9 rounded-full
					@apply border border-Utility-Black body-1 font-bold uppercase font-secondary
					&.btn-add-to-cart
						@apply px-6 body-2
			.wrap-button-add-to-cart
				.product-detail-quantity
					@apply hidden
.section-faq
	.wrap-item-toggle 
		.item-toggle
			@apply border-b border-b-Utility-200 pb-6
			.title
				@apply w-full bg-Utility-white 
				&.active
					i
						&::before
							content: '\f068'	
.product-detail-6
	.button-contact
		a
			span
				@apply body-1 font-secondary
.form-contact
	@apply lg:rem:w-[992px] w-full  relative  xl:p-16 p-5 bg-Utility-50
	.carousel__button
		position: absolute !important
		top: 0rem !important
		right: 0rem !important
		@apply sq-20 z-100 cursor-pointer 
		svg
			@apply rem:w-[28px] rem:h-[28px] text-Primary-1 shadow-none opacity-100
	form.wpcf7-form.submitting
		button
			&[type="submit"]
				@apply pointer-events-none cursor-not-allowed
	.popup-content
		@screen -xl
			@apply p-4
		form
			@apply rem:max-w-[736px] w-full mx-auto
			.wrap-form
				.form-group
					input, textarea
						@apply rem:h-[53px] w-full bg-white inline-flex items-center justify-center px-5
						@apply body-1 font-light placeholder:font-secondary text-black
					&.textarea
						@apply rem:max-h-[86px] h-full
						textarea
							@apply h-full pt-3 resize-none
				.form-submit
					@apply col-span-full mt-3
					.wpcf7-response-output
						@apply hidden				