@layer components
	.indicator-swipe
		@apply z-30 px-3 
		@apply w-[calc(104/19.2*1rem)] h-14 rounded-full
		@apply flex justify-between items-center
		@apply transition-all pointer-events-none
		&:not(.active)
			@apply opacity-0
		svg
			@apply stroke-white
		.hand
			animation: moveSwipeGesture 2.25s infinite ease-in-out
			@apply md:w-16 md:h-12 w-10 h-10
	.nav-primary
		@apply flex items-center justify-center gap-4 
		@apply overflow-auto whitespace-nowrap
		@screen -md
			@apply justify-normal pb-3
		li
			a
				@apply inline-flex items-center justify-center 
				@apply rounded-full px-4 h-10 border border-Utility-Black
				@apply bg-transparent text-black font-semibold uppercase
			&.active, &:hover
				a
					@apply bg-black text-white
	.pagination
		@apply flex items-center justify-center
		ul
			@apply flex items-center justify-center gap-6
			li
				a, span
					@apply inline-flex items-center justify-center
					@apply rem:w-[52px] rem:h-[52px] 
					@apply bg-Utility-300
					@apply font-normal font-secondary
				&.active
					a, span
						@apply bg-Primary-1 text-white
				&:hover
					a
						@apply bg-Primary-1 text-white
	.product-item
		.img
			background: linear-gradient(180deg, #E8E8E8 0%, #DADADA 100%)
			@apply relative
			.img-hover
				@apply absolute inset-0 z-10 opacity-0 transition-opacity duration-300
		&:hover
			.img
				.img-hover
					@apply opacity-100
	.header-button-website
		button
			backdrop-filter: blur(10px)
			background: rgba(0, 0, 0, 0.16)
			@apply inline-flex items-center justify-center
			@apply border rounded-full
			@apply border-white/25 px-9 h-12
			.icon
				@apply sq-6 inline-flex items-center justify-center
				i
					@apply body-4
			span
				@apply body-1 font-secondary font-bold uppercase
	.video-item
		@apply h-full relative
		a
			@apply h-full
		.play-icon
			backdrop-filter: blur(10px)
			@apply absolute top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2 z-20
			@apply text-white bg-black/20 inline-flex items-center justify-center
			@apply rem:w-[68px] rem:h-[68px]  rounded-full heading-1 cursor-pointer
			i
				@apply font-light
	.wrapper-main-collection
		.wrap-button-slide
			.btn-prev
				@apply xl:mr-[-4rem] #{!important}
			.btn-next
				@apply xl:ml-[-4rem] #{!important}
		.wrapper-list
			.col-right
				border-top: 1px solid rgba(56, 56, 56, 0.20)
				.wrap-content
					@apply rem:max-w-[580px] w-full mx-auto h-full
					@apply flex flex-col justify-center
			@screen xl
				&:nth-child(even)
					@apply md:flex-row-reverse flex-col	
			@screen -md
				@apply flex-col gap-base
	.wrapper-icon
		.item
			@apply relative
			&:not(:last-child)
				&::before
					content: ""
					@apply absolute top-0 right-0 bottom-0
					width: 1px
					background: linear-gradient(180deg, rgba(220, 220, 220, 0.00) 0%, #DCDCDC 48.08%, rgba(220, 220, 220, 0.00) 100%)
			@screen -md
				&:not(:last-child)
					&::before
						content: none