    .wrapper-main-collection .wrapper-list:nth-child(even) {
      @apply md:flex-row-reverse flex-col;
    }
  }
  @screen -md {
    .wrapper-main-collection .wrapper-list {
      @apply flex-col gap-base;
    }
  }
  .wrapper-icon .item {
    @apply relative;
  }
  .wrapper-icon .item:not(:last-child)::before {
    content: "";
    @apply absolute top-0 right-0 bottom-0;
    width: 1px;
    background: linear-gradient(180deg, rgba(220, 220, 220, 0) 0%, #DCDCDC 48.08%, rgba(220, 220, 220, 0) 100%);
  }
  @screen -md {
    .wrapper-icon .item:not(:last-child)::before {
      content: none;
    }
  }
}
@layer utilities {
  .section-py {
    @apply py-10 lg:py-15 xl:py-20;
  }
  .no-gutters {
    @apply mx-0;
  }
  .no-gutters > * {
    @apply px-0;
  }
  .lozad {
    opacity: 0;
    filter: blur(10px);
    transition: 0.3s all ease-in-out;
  }
  .lozad[data-loaded=true] {
    opacity: 1;
    filter: blur(0);
  }
}