.page-banner-main
	@apply relative
	// &::before
	// 	content: ""
	// 	@apply w-full h-full top-0 left-0 pointer-events-none absolute z-1
	// 	background: linear-gradient(90deg, #DFE0E3 0%, rgba(223, 224, 227, 0.5) 50%, rgba(223, 224, 227, 0) 100%)
	.content
		@apply absolute top-1/2 left-0 -translate-y-1/2 z-20 w-full
		.title
			@apply xl:rem:text-[80px] heading-1 font-extrabold
		.wrap-content
			@apply rem:max-w-[756px] w-full 
	.global-breadcrumb
		@apply absolute w-full top-0 left-0 bg-transparent z-2
		p
			span
				@apply text-Utility-400 font-secondary
			span.last
				@apply text-black font-bold