.footer
	@apply text-Utility-Black
	.footer-top
		.footer-column
			.footer-slogan
				@apply bg-Primary-1
			ul.footer-menu
				@apply flex flex-col gap-4
				li
					a
						@apply rem:text-[20px] font-normal border-b-2 border-b-transparent font-secondary
					&:hover
						a
							@apply border-b-Primary-1 text-Primary-1
			ul.footer-social-menu
				@apply flex items-center rem:gap-[10px]
				li
					a
						@apply sq-10 inline-flex items-center justify-center rounded-full
						i
							@apply text-Utility-Black font-normal text-2xl
	.footer-bottom
		.footer-policy
			ul
				@apply flex items-center rem:gap-[23px]
				li
					a
						@apply body-4 font-light border-b border-b-transparent
					&:hover
						a
							@apply border-b-black text-black